<template><section><h1>Standard</h1>
<blockquote>
<p>一些基础的规范</p>
</blockquote>
<p>尺寸相关变量在 <code>/src/style/themes/size/index.styl</code> 文件中修改</p>
<h2>Span</h2>
<blockquote>
<p>元素间的距离</p>
</blockquote>
<p><v-code :code="gap.join(' ')"></v-code></p>
<p>基本的 xsm sm md lg xlg 五档间距，UI组件和公共业务组件强制要求，具体页面设计建议遵从。</p>
<h2>Size</h2>
<blockquote>
<p>基础尺寸，一般用于规范元素的高度，比如一个按钮的size。</p>
</blockquote>
<h3>outer</h3>
<p><v-code :code="size_o.join(' ')"></v-code></p>
<h3>inner</h3>
<p><v-code :code="size.join(' ')"></v-code></p>
<p>outer用于Bar组件，inner用于Btn form等组件。</p>
</section></template>

<script>
  import style from '@/style/index.styl';

  export default {
    data() {
      let { gap_xsm, gap_sm, gap_md, gap_lg, gap_xlg, size_sm, size_md, size_lg, size_o_sm, size_o_md, size_o_lg } = style
      return {
        gap: [gap_xsm, gap_sm, gap_md, gap_lg, gap_xlg],
        size: [size_sm, size_md, size_lg],
        size_o: [size_o_sm, size_o_md, size_o_lg]
      }
    }
  }
</script>


